* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*,
*::after,
*::before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

*:focus {
    outline: none;
}

a {
    text-decoration: none;
    color: #ffffff;
}

html {
    scroll-snap-type: y mandatory;
}

body {
    font-family: 'Spoqa Han Sans', 'Malgun Gothic', sans-serif;
    font-weight: 300;
    font-size: 14px;
    line-height: 32px;
    color: #fff;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    margin: 0;
    word-break: keep-all;
}
#header {
    position: absolute;
    width: 100%;
    height: auto;
    z-index: 1;

    #header-logo {
        margin-top: 22vh;
    }

    #pdf {
        position: absolute;
        right: 56px;
        top: 11vh;
        font-weight: bold;
        font-size: 14px;

        i {
            margin-left: 4px;
        }
    }
}

section {
    width: 100vw;
    overflow: hidden;
}

.base-container {
    width: calc(100% - 50%);
    min-width: 1120px;
    margin: 0 auto;
}

.left-side {
    position: absolute;
    left: 56px;
    bottom: 8.8vh;
}

#intro {
    height: 100vh;
    background-image: url("/sources/main-bg.jpg");
    background-size: cover;
    background-position: center;
    display: flex;

    .intro-wrapper {
        align-self: center;

        img {
            max-height: 138px;
            height: 12.7vh;
        }

        p {
            position: absolute;
            bottom: 8.8vh;
        }
    }
}

#section-1 {
    height: auto;
    background: linear-gradient(to bottom, #272727 70%, #27467a);

    .section-1-wrapper {

        .row-1{
            margin-top: 22vh;
            margin-bottom: 6vh;

            img {
                max-height: 39px;
                height: 3.6vh;
            }
        }
    
        .row-2 {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;

            .col-1 {
                width: 40%;

                .button-web {
                    background-color: transparent;
                    color: #ffffff;
                    border: none;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-top: 5.5vh;
                    font-size: 14px;
                    letter-spacing: 0.2em;

                    &:hover {
                        color: #2175ff;
                        transition: color .3s cubic-bezier(.215,.61,.355,1)
                    }

                    i {
                        margin-right: 8px;
                    }
                }
            }

            .col-2 {
                width: 30%;
            
                table {
                    
                    tr {
                        vertical-align: top;

                        td {

                            &:first-of-type {
                                font-weight: bold;
                                padding-right: 16px;
                            }
                        }
                    }
                }
            }
        }
    
        .row-3 {
            position: absolute;
            right: 0;
            width: calc(100% - 25%);
            max-height: 382px;
            height: 30vh;
            margin-top: 11vh;
            display: flex;
            
            .col-img-1 {
                flex-grow: 1.5;
                background-image: url('https://res.cloudinary.com/kyte/image/upload/web/hero-2.jpg');
                background-size: cover;
                background-position: center;
            }

            .col-img-2 {
                flex-grow: 2.5;
                background-image: url('https://res.cloudinary.com/kyte/image/upload/e_grayscale/web/hero-0.jpg');
                background-size: cover;
                background-position: center;
                background-color: #272727;
            }
        }

        .row-4 {
            margin-top: 64vh;
            margin-bottom: 6vh;

            img {
                max-height: 94px;
                height: 8.7vh;
            }
        }

        .row-5 {
            position: relative;

            .history-line {
                position: absolute;
                width: 100%;
                height: 1px;
                background: linear-gradient(to right, rgba(208, 208, 208, 0.6), #2175ff 101%);
                margin-top: 72px;
            }

            .history-content {
                display: flex;
                justify-content: space-between;

                .history-title {
                    font-weight: bold;
                    line-height: 24px;
                    margin-bottom: 64px;
                }

                span {
                    color: rgba(255,255,255,.38);
                }

                span.text-blue {
                    color: #2175ff;
                    font-weight: bold;
                }
            }
        }

        // #row-6 {
        //     position: absolute;
        //     left: 0;
        //     max-height: 382px;
        //     height: 30vh;
        //     margin-top: 22vh;
        //     // background-color: #fff;
        //     display: flex;

        //     #overflow-img-2 {
        //         background-image: url('/sources/img-4.jpg');
        //         background-size: cover;
        //         background-position: center;
        //     }
        // }

        .row-7 {
            position: relative;
            width: 100%;
            
            img {
                position: relative;
                left: 50%;
                transform: translateX(-50%);
                margin: 24vh 0;
                max-height: 38px;
                height: 3.5vh;
            }
        }
    }
}

#section-2 {
    height: 100vh;
    background-color: #2175ff;

    .section-2-wrapper {

        img {
            margin-top: 20vh;
            margin-bottom: 6vh;
            max-height: 134px;
            height: 12.4vh;
        }
    }

    .infographic {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;

        .info-img {

            .info-circle {
                width: 1018px;
                height: 261px;
                border: 1px solid #ffffff;
                border-radius: 130px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                
                .oval {
                    width: 216px;
                    height: 216px;
                    background-color: #146af6;
                    border-radius: 108px;
                    margin: 0 18px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }

            }
        }

        .info-txt {
            width: 1018px;
            display: flex;
            justify-content: space-between;
            margin-top: 80px;

            div {
                width: 260px;
                
                &:first-of-type {
                    margin-left: 18px;
                }
            }
        }
    }
}

#section-3 {
    position: relative;

    .device {
        position: absolute;
        max-width: 390px;
        width: 20vw;
        z-index: 1;

        &:first-of-type {
            left: 50%;
            top: 40vh;
            transform: translateX(-50%);
            filter: drop-shadow(24px 32px 48px rgba(0, 0, 0, .24));
        }
    }

    .border-none {
        position: absolute;
        max-width: 370px;
        width: 19.27vw;
        z-index: 1;
    }

    .sub-1 {
        height: 100vh;
        width: 100vw;
        background-image: url('/sources/sub-bg.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;

        .section-3-wrapper {
            position: relative;
            height: 100vh;

            .title-kyte30 {
                margin-top: 20vh;
                margin-bottom: 6vh;
                filter: drop-shadow(24px 32px 32px rgba(0, 0, 0, .24));
            }
    
            p {
                width: 20vw;
            }

            .small-txt-1 {
                bottom: 10vh;
                position: absolute;

                img {
                    &:first-of-type {
                        margin-right: 72px;
                    }
                }
            }

            .small-txt-2 {
                bottom: 10vh;
                position: absolute;
                right: 0;

                img {
                    &:last-of-type {
                        margin-left: 72px;
                    }
                }
            }
        }
    }

    .sub-2 {
        height: 161.5vh;
        background-color: #c3c3c3;
        position: relative;

        .title-flights {
            position: absolute;
            left: 50%;
            margin-top: 50vh;
            max-height: 95px;
            height: 8.79vh;
        }

        .sub-2-bg {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;

            .bg-oval-1 {
                position: absolute;
                max-width: 828px;
                width: 43vw;
                max-height: 828px;
                height: 43vw;
                background-color: rgba(0,0,0,.04);
                border-radius: 50%;
                top: 25vh;
            }
        }

        .screen-2 {
            left: 0;
            top: 40vh;
            filter: drop-shadow(24px 32px 48px rgba(0, 0, 0, .24));
        }

        .screen-3 {
            left: 50%;
            top: 100vh;
            filter: drop-shadow(24px 32px 48px rgba(0, 0, 0, .24));
        }

        .screen-4 {
            top: 100vh;
        }

        .screen-5 {
            top: 120vh;
        }

        .content-txt {
            color: rgba(0,0,0,.87);
            display: flex;
            justify-content: center;

            &:first-of-type {
                top: 70vh;
            }

            &:nth-of-type(2) {
                position: absolute;
                bottom: 19.5vh;
            }

            div {
                width: 50%;

                &:first-of-type, &:nth-of-type(2) {
                    margin-right: 24px;
                }

                h5 {
                    margin-bottom: 1.5vh;
                    font-size: 16px;
                }
            }
        }
    } 
    
    .sub-3 {
        background-color: #d3d2ce;
        height: 186.8vh;
        position: relative;

        div {

            .title-hotel {
                max-height: 87px;
                height: 8vh;
                margin-top: 44.4vh;
                position: absolute;
                z-index: 1;
            }

            .hotel-1 {
                top: 33.5vh;
                filter: drop-shadow(24px 32px 48px rgba(0, 0, 0, .24));
            }

            .hotel-2 {
                top: 88.8vh;
            }

            .hotel-3 {
                top: 88.8vh;
                filter: drop-shadow(24px 32px 48px rgba(0, 0, 0, .24));
            }

            .hotel-4 {
                top: 77.7vh;
            }

            #hotel-txt-1, #hotel-txt-2 {
                color: rgba(0,0,0,.87);
                display: flex;
                justify-content: center;
                position: absolute;
                z-index: 1;

                &:first-of-type {
                    top: 66.6vh;
                }

                &:nth-of-type(2) {
                    top: 133.3vh;
                }
    
                div {
                    width: 45%;
    
                    &:first-of-type, &:nth-of-type(2) {
                        margin-right: 24px;
                    }
    
                    h5 {
                        margin-bottom: 1.5vh;
                        font-size: 16px;
                    }
                }
            }

            .sub-3-bg {
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                position: relative;
                z-index: 0;

                .bg-oval-2 {
                    position: absolute;
                    max-width: 828px;
                    width: 43vw;
                    max-height: 828px;
                    height: 43vw;
                    background-color: #e4e2da;
                    border-radius: 50%;
                    top: 26vh;
                }
            }
        }
    }
}

#section-4 {
    height: 400vh;
    background-color: #f1f1f1;
    position: relative;

    #magazine-1 {
        max-height: 844px;
        height: 78vh;
        position: absolute;
        left: 50%;
        top: 46vh;
        transform: translateX(-50%);
        z-index: 1;
        filter: drop-shadow(24px 32px 48px rgba(0, 0, 0, .24));
    }

    #magazine-2 {
        top: 135.8vh;
        filter: drop-shadow(24px 32px 48px rgba(0, 0, 0, .24));
    }

    #magazine-3 {
        top: 189.3vh;
        filter: drop-shadow(24px 32px 48px rgba(0, 0, 0, .24));
    }

    #magazine-4 {
        top: 257.8vh;
        right: 50%;
        z-index: 1;
        filter: drop-shadow(24px 32px 48px rgba(0, 0, 0, .24));
    }

    .device {
        position: absolute;
        max-width: 390px;
        width: 20vw;
        z-index: 1;
    }

    .base-container {

        .title-magazine {
            width: 100%;
            position: relative;
            display: flex;
            justify-content: center;

            img {
                position: absolute;

                &:first-of-type {
                    max-height: 96px;
                    height: 8.8vh;
                    top: 22.6vh;
                }

                &:nth-of-type(2) {
                    top: 16.2vh;
                }
            }
        }
    }

    .sub-middle {
        width: 100vw;
        height: 90vh;
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0.13), rgba(0, 0, 0, 0.38));
        position: absolute;
        top: 85vh;
        opacity: 0.24;
    }

    .sub-bottom {
        position: absolute;
        top: 298vh;
        height: 103vh;
        width: 100vw;
        background-color: #fff;

        #logo-black {
            position: absolute;
            top: 71vh;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            width: calc(100% - 50%);
            min-width: 1120px;

            p {
                margin-top: 56px;
                color: rgba(0,0,0,.87);
            }

            .button-container {
                display: flex;
                flex-direction: row;
    
                button {
                    width:176px;
                    height: 56px;
                    background-color: #2175ff;
                    border-radius: 4px;
                    border: none;
                    color: #ffffff;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all .2s ease-out;
                    box-shadow: inset 0 0 0 0 rgba(0,0,0,.87);
                    
                    &:first-of-type {
                        margin-right: 24px;
                    }

                    &:hover {
                        box-shadow: inset 176px 0 0 0 rgba(0,0,0,.87);
                    }

                    i {
                        margin-right: 8px;
                    }
                }
            }
        }

        
    }

    #magazine-txt-1, #magazine-txt-2, #magazine-txt-3 {
        color: rgba(0,0,0,.87);
        position: absolute;

        h5 {
            margin-bottom: 1.5vh;
            font-size: 16px;
        }
    }

    #magazine-txt-2 {
        left: 50%;
        top: 146.6vh;
    }

    #magazine-txt-3 {
        display: flex;
        justify-content: center;
        top: 235.4vh;

        div {
            width: 50%;

            &:first-of-type, &:nth-of-type(2) {
                margin-right: 24px;
            }
        }
    }
}
